import { vIntersectionObserver as IntersectionObserver } from "@vueuse/components";
import IconDelete from "vue-material-design-icons/Delete.vue";
import IconPoll from "vue-material-design-icons/Poll.vue";
import { t, n } from "@nextcloud/l10n";
import NcButton from "@nextcloud/vue/dist/Components/NcButton.js";
import { POLL } from "../../../../../constants.js";
import { usePollsStore } from "../../../../../stores/polls.ts";
export default {
  name: "Poll",
  components: {
    NcButton,
    IconDelete,
    IconPoll
  },
  directives: {
    IntersectionObserver
  },
  props: {
    name: {
      type: String,
      required: true
    },
    id: {
      type: String,
      required: true
    },
    token: {
      type: String,
      required: true
    },
    showAsButton: {
      type: Boolean,
      default: false
    },
    draft: {
      type: Boolean,
      default: false
    }
  },
  emits: ["click"],
  setup() {
    return {
      pollsStore: usePollsStore()
    };
  },
  computed: {
    poll() {
      return !this.draft ? this.pollsStore.getPoll(this.token, this.id) : this.pollsStore.drafts[this.token][this.id];
    },
    pollFooterText() {
      if (this.poll?.status === POLL.STATUS.OPEN) {
        return this.poll?.votedSelf.length > 0 ? t("spreed", "Open poll \u2022 You voted already") : t("spreed", "Open poll \u2022 Click to vote");
      } else if (this.draft) {
        return n("spreed", "Poll draft \u2022 %n option", "Poll draft \u2022 %n options", this.poll?.options?.length);
      } else {
        return this.poll?.status === POLL.STATUS.CLOSED ? t("spreed", "Poll \u2022 Ended") : t("spreed", "Poll");
      }
    }
  },
  methods: {
    t,
    n,
    getPollData() {
      if (!this.poll) {
        this.pollsStore.getPollData({
          token: this.token,
          pollId: this.id
        });
      }
    },
    openDraft() {
      this.$emit("click", this.id);
    },
    deleteDraft() {
      this.pollsStore.deletePollDraft({
        token: this.token,
        pollId: this.id
      });
    },
    openPoll() {
      this.pollsStore.setActivePoll({
        token: this.token,
        pollId: this.id,
        name: this.name
      });
    }
  }
};
